        <el-row>
            <!--<el-button size="mini" @click="drawPolygon"><i class="iconfont">&#xe6ec;</i>Â§öËæπÂΩ¢</el-button>
            <el-button size="mini" @click="drawPolygonLine">ËøûÁ∫ø</el-button>
            <el-button size="mini" @click="drawPolygonAlongRoad">Ê≤øË∑ØÁªòÂà∂</el-button>
            <el-button size="mini" @click="drawPolygonAlongRoadOver">Ê≤øË∑ØÁªòÂà∂ÂÆåÊàê</el-button>
            <el-button size="mini" @click="drawPolygonLine">ËøûÁ∫ø</el-button>
            <el-button size="mini" @click="editLineOrPloygon">ÁºñËæëÁ∫ø„ÄÅÈù¢</el-button>
            <el-button size="mini" @click="show">Â±ïÁ§∫Ê†ÖÊ†º</el-button>-->
            <el-col :span="12">
                <div class="grid-content bg-purple-dark">
                    <el-dropdown size="small" trigger="click" @command="handleCommand">
                        <el-button type="primary">
                            ÁªòÂà∂Â∑•ÂÖ∑<i class="el-icon-arrow-down el-icon--right"></i>
                        </el-button>

                        <el-dropdown-menu slot="dropdown">
                            <el-dropdown-item command="r">Áü©ÂΩ¢</el-dropdown-item>
                            <el-dropdown-item command="p">Â§öËæπÂΩ¢</el-dropdown-item>
                            <el-dropdown-item command="l">ËøûÁ∫ø</el-dropdown-item>
                        </el-dropdown-menu>
                    </el-dropdown>
                </div>
            </el-col>

            <el-col :span="12">
                <div class="grid-content bg-purple-dark">
                    <el-dropdown size="small" trigger="click">
                        <el-button type="primary">
                            ËµÑÊ∫êÂ±ïÁ§∫<i class="el-icon-arrow-down el-icon--right"></i>
                        </el-button>
                        <el-dropdown-menu slot="dropdown">
                            <el-dropdown-item>
