<script>
    export default {
        name: "BmapTools.vue",
        data() {
            return {
                showView: {
                    isShowShange: false,
                    isShowRivalChl:false
                }
            }
        },
        methods: {
            handleCommand(command) {
                let that = this;
                switch (command) {
                    case "a":
                        var flag = that.showView.isShowShange
                        this.$emit("showGrid", flag)
                        break;
                    case "p":
                        this.$emit("drawPolygonEvent")
                        break;
                    case "l":
                        this.$emit("drawPolygonLineEvent")
                        break;
                    case "r":
                        this.$emit("drawRectangleEvent")
                        break;
                    default:
                        break
                }
            },

            /**
             * Â±ïÁ§∫Ê†ÖÊ†º
             * */
            showShange() {
                let that = this;
                var flag = that.showView.isShowShange
                if (!flag) {
                    this.$emit("showGrid", flag)
                    return;
                }
                this.$emit("showGrid", flag)
            },

            /**
             * Â±ïÁ§∫Á´û‰∫âÂØπÊâã
             * */
            showRivalChl(){
                let that = this;
                var flag = that.showView.isShowRivalChl
                if (!flag) {
                    this.$emit("showRivalChl", flag)
                    return;
                }
                this.$emit("showRivalChl", flag)
            },

            /**
             * Â§öËæπÂΩ¢ÁªòÂà∂
             */
            drawPolygon() {
                this.$emit("drawPolygonEvent")
            },
            /**
             * ËøûÁ∫øÁªòÂà∂
             */
            drawPolygonLine() {
                this.$emit("drawPolygonLineEvent")
            },
            /**
             * Ê≤øË∑ØÁªòÂà∂
             */
