            },
            /**
             * Ê≤øË∑ØÁªòÂà∂ÂÆåÊØï
             */
            drawPolygonAlongRoadOver() {
                this.$emit("drawPolygonAlongRoadOverEvent")
            },
            editLineOrPloygon() {
                this.$emit("editLineOrPloygonEvent")
            }
        }
    }
</script>

