import Vue from 'vue';
import VueRouter from 'vue-router';
import demoComponents from './demo-components';
import { isAuthPage } from './menu-config';
import Login from "../../views/login/Login";
Vue.use(VueRouter);

const initRouter = () => {
  const routerParam = {
    mode: 'history',
    routes: [{
      path: '/login',
      name: 'Login',
      component: Login
    }, {
      path: '/',
      component: (resolve) => require(['components/app/app-frame'], resolve),
      children: [{
        path: '',
        name: 'Home',
        component: (resolve) => require(['components/home/index'], resolve),
        meta: { title: 'È¶ñÈ°µ', icon: 'icon-monitor' }
      }, {
        path: '/system-error',
        name: 'SystemError',
        component: (resolve) => require(['components/error-pages/500'], resolve),
        meta: { title: 'Á≥ªÁªüÈîôËØØ' }
      }, {
        path: '/permission-error',
        name: 'PermissionError',
        component: (resolve) => require(['components/error-pages/403'], resolve),
        meta: { title: 'ÊùÉÈôêÈîôËØØ' }
      },
      {
        path: '/notfound-error',
        name: 'NotfoundError',
        component: (resolve) => require(['components/error-pages/404'], resolve),
        meta: { title: 'È°µÈù¢Êâæ‰∏çÂà∞' }
      }, {
        path: '/authorization',
        name: 'Authorization',
        component: (resolve) => require(['components/management/authorization'], resolve),
        meta: { title: 'ÊùÉÈôêÁÆ°ÁêÜ' }
      }, {
        path: '/users',
        name: 'Users',
        component: (resolve) => require(['components/management/users'], resolve),
        meta: { title: 'Áî®Êà∑ÁÆ°ÁêÜ' }
      },
      ...demoComponents,
      {
        path: '*',
        name: 'CommonNotfoundError',
        component: (resolve) => require(['components/error-pages/404'], resolve),
        meta: { title: 'È°µÈù¢Êâæ‰∏çÂà∞' }
      }]
    }]
  };

  let router = new VueRouter(routerParam);
  let isFirstRouter = true;

  router.beforeEach((to, from, next) => {
    if (!isFirstRouter && !isAuthPage(to.name)) {
      next({ name: 'PermissionError' });
      return;
    }
    HeyUI.$LoadingBar.start();
    if (to.meta && to.meta.title) {
      document.title = to.meta.title + ' - ÁÆ°ÁêÜÂ∫îÁî®';
    } else {
      document.title = 'ÁÆ°ÁêÜÁ≥ªÁªü';
    }
    isFirstRouter = true;
    next();
  });
  router.afterEach(() => {
    HeyUI.$LoadingBar.success();
    document.documentElement.scrollTop = 0;
    document.body.scrollTop = 0;
    let layoutContent = document.querySelector('.h-layout-content');
    if (layoutContent) {
      layoutContent.scrollTop = 0;
    }
    // baidu ÁªüËÆ°ÔºåÂ¶ÇÊûúÊúâËá™Â∑±ÁöÑÁªüËÆ°ÔºåËØ∑Ëá≥index.html‰øÆÊîπËá≥Ëá™Â∑±ÁöÑÂüãÁÇπ
    if (window._hmt) {
      window._hmt.push(['_trackPageview', window.location.pathname]);
    }
  });
  return router;
};

export default initRouter;
