<script>
import FormModel from 'model/Form';
import categoryList from './components/datas/data5';
export default {
  data() {
    return {
      mode: 'single',
      data: FormModel.parse({}),
      dataParam: {
        1: 'Áî∑',
        2: 'Â•≥',
        3: 'ÂÖ∂‰ªñ'
      },
      param1: ['ÁæéÈáë', '‰∫∫Ê∞ëÂ∏Å', 'Âç¢Â∏É'],
      categoryParam: {
        title: 'ÊµãËØï',
        keyName: 'id',
        titleName: 'name',
        dataMode: 'tree',
        datas: categoryList
      },
      isLoading: false,
      validationRules: {
        rules: {
          textarea: {
            maxLen: 50,
            minLen: 10
          },
          input: {
            valid(value) {
              if (value.length == 15 || value.length == 18) {
                return true;
              } else {
                return 'Â≠óÊÆµÈïøÂ∫¶Èùû15/18‰ΩçÔºåÂèØËÉΩ‰∏çÁ¨¶ÂêàËßÑÂÆö';
              }
            }
          }
        },
        required: [
          'autocomplete',
          'category',
          'select2',
          'select3',
          'inputs[].value',
          'input',
          'radio',
          'rate',
          'checkbox',
          'money',
          'date',
          'taginputs',
          'money.min',
          'money.max',
          'int',
          'number',
          'url',
          'email',
          'tel',
          'mobile'
        ],
        int: ['int'],
        number: ['number', 'money.min', 'money.max'],
        url: ['url'],
        email: ['email'],
        tel: ['tel'],
        mobile: ['mobile'],
        combineRules: [
          {
            parentRef: 'money',
            refs: ['min', 'max'],
            valid: {
              valid: 'lessThan',
              message: 'Ëµ∑ÂßãÈáëÈ¢ù‰∏çËÉΩÂ§ß‰∫éÁªìÊùüÈáëÈ¢ù'
            }
          }
        ]
      }
    };
  },
  methods: {
    submit() {
      let validResult = this.$refs.form.valid();
      if (validResult.result) {
        this.$Message('È™åËØÅÊàêÂäü');
        this.isLoading = true;
        setTimeout(() => {
          this.isLoading = false;
        }, 1000);
      }
    },
    reset() {
      this.isLoading = false;
      this.$refs.form.resetValid();
    },
    resetData() {
      this.isLoading = false;
      this.$refs.form.resetValid();
      this.data = FormModel.parse({});
    },
    add() {
      this.data.inputs.push({ value: '' });
    },
    remove(index) {
      this.data.inputs.splice(index, 1);
    }
  }
};
</script>
