<template>
    <div>
        <div class="table-basic-vue frame-page h-panel" v-loading="allLoading">
            <div class="h-panel-bar"><span class="h-panel-title">Â∑•‰ΩúËÆ°ÂàíÂÆâÊéí</span></div>
            <div class="h-panel-body">
                <FullCalendar
                        :config="config"
                        :events="events"
                        ref="calendar"
                ></FullCalendar>
            </div>
        </div>

        <!--Êñ∞Â¢ûÊó•Á®ãÂºπÁ™ó-->
        <Modal v-model="addPlanModalFlag" v-bind="addPlanModalParams">
            <div slot="header">Ê∑ªÂä†Êó•Á®ãÔºö{{addPlanModalParams.currSelectDate}}</div>
            <div :style="{'height': height ? '800px' : 'auto','width':'400px'}" v-loading="addNewWorkPlanLoading">
                <Form
                        ref="dayEventForm"
                        labelPosition="left"
                        :labelWidth="80"
                        :model="dayEventFormInfo"
                        :rules="dayEventFormInfo.rules"
                >
                    <FormItem label="Êó•Á®ãÂêçÁß∞" prop="name">
                        <input type="text" v-model="dayEventFormInfo.name">
                    </FormItem>
                    <FormItem label="Êó∂Èó¥" prop="dateTime">
                        <DateRangePicker :value="dayEventFormInfo.dateTime" v-model="dayEventFormInfo.dateTime"
                                         :format="format" type="datetime"></DateRangePicker>
                    </FormItem>
                    <FormItem label="Êó•Á®ãÂÜÖÂÆπ" prop="content">
                        <textarea v-model="dayEventFormInfo.content"></textarea>
                    </FormItem>
                    <FormItem label="Â§áÊ≥®" prop="remark">
                        <textarea v-model="dayEventFormInfo.remark"></textarea>
                    </FormItem>
                </Form>
            </div>
            <div slot="footer">
                <button class="h-btn" @click="close">ÂèñÊ∂à</button>
                <button class="h-btn h-btn-primary" @click="confirm" :disabled="addNewWorkPlanLoading">Á°ÆÂÆö</button>
            </div>
        </Modal>

        <!--ÂΩìÂ§©Êó•Á®ãÂàóË°®-->
        <Modal v-model="showDayEventsModalParams.openFlag" v-bind="showDayEventsModalParams">
            <div slot="header">ÂΩìÂ§©Êó•Á®ãÂÆâÊéíË°®Ôºö{{showDayEventsModalParams.currSelectDate}}</div>
            <div :style="{'height': false ? '300px' : 'auto','width':'400px'}" v-loading="addNewWorkPlanLoading">
                <el-timeline>
                    <el-timeline-item :timestamp="item.start" placement="top" v-for="(item,index) in dayEventList" :key="index" :color="item.color">
                        <el-card>
                            <h4>{{item.title}}</h4>
                            <p>{{item.content}}</p>
                            <p>{{item.start}}&nbsp;&nbsp;{{item.end}}</p>
                        </el-card>
                    </el-timeline-item>
                </el-timeline>
            </div>
        </Modal>
    </div>
</template>

<script>
    import {FullCalendar} from 'vue-full-calendar';
    import {addNewWorkPlan, queryWorkPlanList} from "../js/request/workPlanReq";
    import "../js/common/message";
    import {showErrorMessage, showSuccessMessage} from "../js/common/message";
    import moment from 'moment';
    export default {
        name: "WorkPlan",
        components: {
            FullCalendar: FullCalendar
        },
        data() {
            return {
                allLoading:false,
                addPlanModalFlag: false,
                addPlanModalParams: {
                    closeOnMask: true,
                    fullScreen: false,
                    middle: true,
                    hasMask: true,
                    hasDivider: false,
                    hasCloseIcon: true,
                    draggable: true,
                    currSelectDate: ''
                },
                showDayEventsModalParams:{
                    closeOnMask: true,
                    fullScreen: false,
                    middle: true,
                    hasMask: true,
                    hasDivider: false,
                    hasCloseIcon: true,
                    draggable: false,
                    openFlag: false,
                    currSelectDate:''
                },
                dayEventList:[],
                height: false,
                format: 'YYYY-MM-DD HH:mm:ss',
                dayEventFormInfo: {
                    name: '',
                    content: '',
                    dateTime: null,
                    remark: '',
                    rules: {required: ['name', 'content']}
                },
                addNewWorkPlanLoading: false,
                selectDate: '',//Êó•ÊúüÈÄâÊã©Âô®ÈÄâ‰∏≠ÁöÑÊúà‰ªΩ
                events: [],
                config: {
                    firstDay: '1',//‰ª•Âë®Êó•‰∏∫ÊØèÂë®ÁöÑÁ¨¨‰∏ÄÂ§©
                    buttonText: {today: "‰ªäÂ§©", month: "Êúà", week: "Âë®", day: "Êó•"},
                    header: {left: 'today', center: 'prev, title, next'},
                    theme: false,//ÊòØÂê¶ÂÖÅËÆ∏‰ΩøÁî®jqueryÁöÑui‰∏ªÈ¢ò
                    height: 'auto',
                    slotLabelFormat: 'H:mm', // axisFormat 'H(:mm)'
                    //slotLabelInterval:1,
                    views: {
                        month: {
                            titleFormat: 'YYYYÂπ¥MMM'
                        },
                        day: {
                            titleFormat: 'YYYYÂπ¥MMMDDÊó• dddd'
                        }
                    },
                    monthNames: ["1Êúà", "2Êúà", "3Êúà", "4Êúà", "5Êúà", "6Êúà", "7Êúà", "8Êúà", "9Êúà", "10Êúà", "11Êúà", "12Êúà"],
                    monthNamesShort: ["1Êúà", "2Êúà", "3Êúà", "4Êúà", "5Êúà", "6Êúà", "7Êúà", "8Êúà", "9Êúà", "10Êúà", "11Êúà", "12Êúà"],
                    dayNames: ["ÊòüÊúüÊó•", "ÊòüÊúü‰∏Ä", "ÊòüÊúü‰∫å", "ÊòüÊúü‰∏â", "ÊòüÊúüÂõõ", "ÊòüÊúü‰∫î", "ÊòüÊúüÂÖ≠"],
                    dayNamesShort: ["ÊòüÊúüÊó•", "ÊòüÊúü‰∏Ä", "ÊòüÊúü‰∫å", "ÊòüÊúü‰∏â", "ÊòüÊúüÂõõ", "ÊòüÊúü‰∫î", "ÊòüÊúüÂÖ≠"],
                    slotDuration: '00:30:00',
                    minTime: '00:00',
                    maxTime: '24:00',
                    locale: "zh-cn",
                    editable: false, //ÊòØÂê¶ÂÖÅËÆ∏‰øÆÊîπ‰∫ã‰ª∂
                    selectable: true,//ÊòØÂê¶ÂÖÅËÆ∏Áî®Êà∑ÂçïÂáªÊàñËÄÖÊãñÊãΩÊó•ÂéÜ‰∏≠ÁöÑÂ§©ÂíåÊó∂Èó¥Èöô
                    eventLimit: true, // ÈôêÂà∂‰∏ÄÂ§©‰∏≠ÊòæÁ§∫ÁöÑ‰∫ã‰ª∂Êï∞ÔºåÈªòËÆ§false
                    allDaySlot: true, //ÊòØÂê¶ÊòæÁ§∫allDay
                    displayEventEnd: true,//ÊòØÂê¶ÊòæÁ§∫ÁªìÊùüÊó∂Èó¥
                    allDayText: 'ÂÖ®Â§©',
                    navLinks: true, //ÂÖÅËÆ∏Â§©/Âë®ÂêçÁß∞ÊòØÂê¶ÂèØÁÇπÂáª
                    defaultView: "month", //ÊòæÁ§∫ÈªòËÆ§ËßÜÂõæmonth,agendaWeek,agendaDay,list
                    eventClick: this.eventClick, //ÁÇπÂáª‰∫ã‰ª∂
                    dayClick: this.dayClick, //ÁÇπÂáªÊó•Á®ãË°®‰∏äÈù¢Êüê‰∏ÄÂ§©
                    eventRender: this.eventRender

                },
                queryWorkPlanParams:{
                    period:'m',
                    day:'',
                    week:'',
                    month:''
                }
            }
        },
        mounted() {
            this.allLoading=true;
            this.getWorkPlan('m')
        },
        methods: {
            changeDate() {
                this.$refs.calendar.fireMethod('gotoDate', this.selectDate)
            },
            // ÁÇπÂáª‰∫ã‰ª∂
            eventClick(event, jsEvent, pos) {
                //alert('eventClick', event, jsEvent, pos)
                let _this=this
                _this.showDayEventsModalParams.openFlag=true
                _this.showDayEventsModalParams.currSelectDate=moment(event.start).format('YYYY-MM-DD')
                _this.queryWorkPlanParams.period='d'
                _this.queryWorkPlanParams.day=moment(event.start).format('DD')
                queryWorkPlanList(_this.queryWorkPlanParams).then(res => {
                    if (res.data.errorCode == '0000') {
                        let dayEventList = res.data.body
                        _this.dayEventList=dayEventList
                    } else {
                        showErrorMessage("Â∑•‰ΩúËÆ°ÂàíÊü•ËØ¢Â§±Ë¥•")
                    }
                }).catch(err => {
                    showErrorMessage("Â∑•‰ΩúËÆ°ÂàíÊü•ËØ¢Â§±Ë¥•")
                })


            },
            // ÁÇπÂáªÂΩìÂ§©
            dayClick(day, jsEvent) {
                //alert('dayClick', day, jsEvent)
                let currentDate = day.format()
                this.addPlanModalParams.currSelectDate = currentDate
                this.addPlanModalFlag = true
            },
            eventRender: function (event, element) {
                element.find('.fc-title').attr('title', event.title)
            },


            /**
             * ÂΩìÂâç‰∫ã‰ª∂ÊéßÂà∂
             */
            close() {
                this.addPlanModalFlag = false
            },
            confirm() {
                let _this = this;
                let validResult = _this.$refs.dayEventForm.valid();
                if (validResult.result) {
                    let addNewWorkPlanParams = {
                        planName: _this.dayEventFormInfo.name,
                        planContent: _this.dayEventFormInfo.content,
                        startTime: null,
                        endTime: null,
                        remark: _this.dayEventFormInfo.remark
                    }
                    let dateTimeRange = _this.dayEventFormInfo.dateTime
                    if (dateTimeRange !== '' && dateTimeRange !== undefined && dateTimeRange !== null) {
                        addNewWorkPlanParams.startTime = dateTimeRange.start
                        addNewWorkPlanParams.endTime = dateTimeRange.end
                    } else {
                        let currentDate = new Date(_this.addPlanModalParams.currSelectDate);
                        addNewWorkPlanParams.startTime = currentDate
                        addNewWorkPlanParams.endTime = currentDate
                    }
                    _this.addNewWorkPlanLoading = true;
                    addNewWorkPlan(addNewWorkPlanParams).then(res => {
                        _this.addNewWorkPlanLoading = false;
                        if (res && res.data.errorCode == '0000') {
                            _this.addPlanModalFlag = false
                            showSuccessMessage('Âú®' + _this.addPlanModalParams.currSelectDate + 'Êñ∞Â¢û‰∏ÄÊù°Êó•Á®ã„Äê' + _this.dayEventFormInfo.name + '],Âç≥Â∞ÜÊî∂Âà∞ÈÇÆ‰ª∂ÊàñËÄÖÁü≠‰ø°ÊèêÁ§∫ÔºÅ')
                            _this.allLoading=true
                            _this.getWorkPlan('m')
                        }
                    }).catch(err => {
                        _this.addNewWorkPlanLoading = false;
                        _this.addPlanModalFlag = false
                        showErrorMessage('Êñ∞Â¢ûÊó•Á®ãÈîôËØØ!')
                    })
                } else {
                    showErrorMessage('Êñ∞Â¢ûÊó•Á®ãÈîôËØØ!')
                }
            },

            getWorkPlan(period) {
                let _this = this
                _this.queryWorkPlanParams.period=period
                queryWorkPlanList(_this.queryWorkPlanParams).then(res => {
                    _this.allLoading=false;
                    if (res.data.errorCode == '0000') {
                        let workPlanList = res.data.body
                        _this.events = workPlanList
                    } else {
                        showErrorMessage("Â∑•‰ΩúËÆ°ÂàíÊü•ËØ¢Â§±Ë¥•")
                    }
                }).catch(err => {
                    _this.allLoading=false;
                    showErrorMessage("Â∑•‰ΩúËÆ°ÂàíÊü•ËØ¢Â§±Ë¥•")
                })
            }
        }
    }
</script>

